nav {
  background-color: #000;
  padding: 20px;
}

.navitens {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-left: 2rem;
  justify-content: space-around;
}

.socialLinks {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-left: 2rem;
}
.socialLinks a {
  margin: 0.4rem;
}
.socialLinks a img {
  width: 2rem;
}

.centerImage {
  display: flex;
  margin-left: auto;
}

.centerImage img {
  width: 6rem;
}

.internalLinks {
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin-left: auto;
}

.internalLinks h2 {
  font-size: 1rem;
}

.internalLinks a {
  color: #fff;
  text-decoration: none;
  margin: 0 1rem;
}

.internalLinks a:hover {
  color: #f60;
}
